/**
 * The following styles get applied inside the editor only.
 * Replace them with your own styles or remove the file completely.
 */

.wp-block-create-block-texonomy-menu-accordion {
    // padding: 2px;
}

/* ১. প্যানেল হেডার সেটিংস */
.block-editor-inserter__panel-header {
    display: flex !important;
    align-items: start;
    justify-content: flex-start;
    /* টাইটেলের পাশে আইকন রাখার জন্য */
}

/* ২. টাইটেল এর ওপর আইকন বসানো (স্ক্রিনশট অনুযায়ী এই ক্লাসটিই কাজ করছে) */
.block-editor-inserter__panel-title::after {
    content: "";
    display: inline-block;
    width: 16px;
    height: 16px;
    margin-left: 10px;
    background-image: url('./../../assets/icons/Inserter-category-icon.svg');
    background-size: contain;
    background-repeat: no-repeat;
    vertical-align: middle;
}

/* ৩. ফিল্টার লজিক */
/* যদি এই হেডারের পরের কন্টেন্টে EASY ACCORDION না থাকে, তবে আইকন মুছে দাও */
/* :not(:has bole je jodi na thake then oita koro etc ! */
.block-editor-inserter__panel-header:not(:has(+ .block-editor-inserter__panel-content [aria-label="EASY ACCORDION"])) .block-editor-inserter__panel-title::after {
    display: none !important;
    /* !important দিন যাতে অন্য স্টাইল একে বাধা না দেয় */
}


/* accordion classes styles */



/* Target the root block wrapper to ensure it centers in the editor */
[data-type="create-block/texonomy-menu-accordion"] {
    margin-top: 0 !important;
    /* Removes top margin */
    margin-bottom: 0 !important;
    /* Removes bottom margin */
    display: flex;
    justify-content: center;
    /* This centers the box inside the 750px wide container */
    width: 350px;
    //  max-width: 150px
}

.ea-main-editor-container {
    width: 100%;
    max-width: 450px;
    padding: 2px;
    // background: #fff;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.05);
    font-family: sans-serif;
    border: 0 !important;

    /* Clean Underline Styling */
    .ea-editor-preview-list {
        list-style: none;
        padding: 0;
        margin: 0;


        .ea-editor-item {

            /* Parent: Light Gray */
            .ea-item-header {
                background: #ebebeb;
                padding: 15px 20px;
                display: flex;
                justify-content: space-between;
                align-items: center;
                border-bottom: 1px solid rgb(155, 155, 155);


                .ea-title {
                    font-size: 18px;
                    color: #333;
                     /* Child count in parentheses */
                    .sub-count {
                        color: #888;
                        margin-left: 5px;
                        font-weight: normal;
                    }
                }

                .ea-icon-wrapper {
                    display: flex;
                    align-items: center;
                    transition: transform 0.3s ease;
                    /* এটিই ম্যাজিক */
                    color: #666;

                    &.is-open {
                        transform: rotate(-180deg);
                        /* ক্লিক করলে অ্যারো ঘুরে যাবে */
                    }
                }
            }

            /* Child: Darker Gray Shade */
            .ea-editor-submenu {


                .ea-item-header {
                    background: transparent;
                    padding-left: 40px;
                    /* Indent child items */
                    border-bottom: 1px solid #828282;

                    .ea-title {
                        font-size: 14px;
                        color: #555;
                    }
                }
            }
        }
    }
}